* {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

html,
body {
  min-height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}

main {
  flex: 1 0 auto;
}

footer {
  flex: 0 0 auto;
}

li {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: inherit;
  font-weight: inherit;
}

span {
  color: inherit;
  font-weight: inherit;
}

button {
  background: none;
  border: none;
  cursor: pointer;
}

.svg-icon {
  fill: inherit;
  stroke: inherit;
  width: 32px;
  height: 32px;
}

body > .ui-loader.ui-corner-all.ui-loader-default.ui-body-a {
  display: none;
}

html,
body {
  overflow-x: hidden;
  width: 100vw;
  max-width: 100vw;
}

body {
  background: #111;
  color: white;
  font-family: "Montserrat";
  font-weight: 500;
}

.container {
  box-sizing: border-box;
  max-width: 1676px;
  padding: 0 20px;
  margin: 0 auto;
}

.slider {
  overflow-x: hidden;
}

.text-show-anim {
  opacity: 0;
  transform: translateY(20px);
  transition: 1000ms ease-in-out;
  transition-delay: 0.2s;
}
.text-show-anim.--show {
  opacity: 1;
  transform: translateY(0);
}
.text-show-anim._horizontal {
  transform: translateY(0);
  transform: translateX(-20px);
}
.text-show-anim._horizontal.--show {
  transform: translateX(0);
}
.text-show-anim._horizontal-reverse {
  transform: translateY(0);
  transform: translateX(20px);
}
.text-show-anim._horizontal-reverse.--show {
  transform: translateX(0);
}

.gradient {
  position: absolute;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  filter: blur(125px);
  z-index: -1;
}

.page__subtitle {
  color: #FFF;
  font-size: 60px;
  font-weight: 600;
  line-height: 150%; /* 90px */
  text-transform: uppercase;
}

.page__subdescr {
  color: #FFF;
  font-size: 25px;
  font-weight: 400;
  line-height: 150%; /* 37.5px */
}

@media screen and (max-width: 625px) {
  .page__subtitle {
    font-size: 40px;
  }
  .page__subdescr {
    font-size: 20px;
  }
}
@media screen and (max-width: 430px) {
  .page__subtitle {
    font-size: 30px;
  }
  .page__subdescr {
    font-size: 15px;
  }
}
.btn-install {
  display: flex;
  border-radius: 20px;
  background: #FFF;
  padding: 9px;
  gap: 9px;
  align-items: center;
  width: 130px;
  box-sizing: border-box;
}

.btn-install__icon {
  display: flex;
}

.btn-install__label {
  color: #000;
  font-family: "Fuzzybubbles";
  font-size: 12px;
  font-weight: 700;
  line-height: 15px;
}

.btn-install__label-install {
  color: #19B8FA;
  font-size: 10px;
  font-weight: 400;
}

.btn-ticket {
  position: relative;
  display: flex;
  align-items: center;
  justify-self: center;
  justify-content: center;
  width: 242px;
  height: 242px;
}
.btn-ticket:hover .btn-ticket__circled-text {
  fill: white;
}
.btn-ticket:hover .btn-ticket__label {
  opacity: 100;
}
.btn-ticket:hover .btn-ticket__bg::after {
  left: -100%;
}

.btn-ticket__bg {
  position: absolute;
  width: 153px;
  height: 160px;
  border-radius: 100%;
  overflow: hidden;
  background: #0145D4;
  transform: rotate(65.16deg);
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn-ticket__bg::after {
  content: "";
  position: absolute;
  width: 153px;
  height: 160px;
  left: 0;
  transition: 500ms ease-in-out left;
  background: linear-gradient(90deg, #9AB4F8 0%, #0145D4 83.65%);
}

.btn-ticket__circled-text {
  position: absolute;
  z-index: 1;
  top: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 16.3px;
  font-weight: 800;
  line-height: 100%; /* 16.3px */
  fill: #0145D4;
  transition: 500ms ease-in-out fill;
  animation-name: btn-circled-text;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

.btn-ticket__label {
  opacity: 0;
  z-index: 2;
  width: 102px;
  margin-top: -4px;
  transition: 500ms ease-in-out opacity;
  color: #FFF;
  text-align: center;
  font-size: 16.3px;
  font-weight: 600;
  line-height: 150%; /* 24.449px */
}
.btn-ticket__label span {
  font-weight: 800;
}

@keyframes btn-circled-text {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
